.aboutMeContainer {
  background-color: rgb(36, 36, 36);
  min-height: 90vh;
  margin: 0;
  border-bottom: 1px solid #d8d8d8da;
  padding: 3rem 2rem;
  color: #d8d8d8da;
  font-family: 'Cousine', monospace;
  border: 2px solid rgb(255, 94, 0);
  display: flex;
  flex-direction: column;
  align-items: center;
}

.aboutText {
  max-width: 1200px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.aboutTitle {
  font-size: 5rem;
  -ms-flex-align: start;
  margin: 4rem 0 2rem 0
}

.aboutP {
  font-size: 1.5rem;
}

.aboutTech {
  font-size: 1.5rem;
}

.aboutMeContainer li {
  font-size: 1.4rem;
  margin: 0 0 0 1rem;
}

@media only screen and (max-width: 1400px) {
  .aboutText {
    max-width: 900px;
  }
  .aboutTitle {
    font-size: 4rem;
  }
}

@media only screen and (max-width: 720px) {
  .aboutTitle {
    font-size: 2.5rem;
  }
  .aboutP {
    font-size: 1.2rem;
  }
  .aboutTech {
    font-size: 1.2rem;
  }
  .aboutMeContainer li {
    font-size: 1rem;
  }
}